.sec6 {
    background-image: url('bg.jpg');
    background-size: cover;
    background-position: center bottom;
    padding-top: 200 / $setFs + rem;
    padding-bottom: 650 / $setFs + rem;

    @media screen and (max-width: 768px) {
        padding-top: 50 / $setFs + rem;
        padding-bottom: 0;

    }


    &:before {
        @media screen and (max-width: 768px) {
            content: '';
            width: 100%;
            height: 100%;
            background-image: url('bg.jpg');
            background-size: cover;
            background-position: center bottom;
            position: absolute;
            top: 0;
            left: 0;
            transform: scaleY(-1);
        }
    }
}

.sec6_container {
    display: flex;
    justify-content: flex-end;
}

.sec6_content {
    max-width: 520 / $setFs + rem;
    margin-right: -60 / $setFs + rem;

    @media screen and (max-width: 768px) {
        margin-right: auto;
        right: -5%;
    }

    @media screen and (max-width: 480px) {
        margin-right: auto;
        right: 0;
    }
}

.sec6_river {
    width: 100%;
    position: absolute;
    bottom: 0%;
    left: 0;
    mask-image: linear-gradient(140deg, black 0%, black 0%, transparent 0%);

    @media screen and (max-width: 768px) {
        bottom: 10%;
    }

    @media screen and (max-width: 480px) {
        width: 130%;
        bottom: 5%;
    }
}

.sec6_letters {
    width: 2543 / 3543 * 100%;
    position: absolute;
    bottom: 8.5%;
    right: .6%;
}

.sec6_peo_1 {
    width: 610 / $setFs + rem;
    position: absolute;
    top: 100 / $setFs + rem;
    left: 280 / 1920 * 100%;

    @media screen and (max-width: 768px) {
        width: 50%;
        position: relative;
        top: auto;
        left: 7%;
        margin-bottom: -150 / $setFs + rem;
    }

    @media screen and (max-width: 480px) {
        width: 70%;
        margin-bottom: -40 / $setFs + rem;
    }

    .dot {
        top: 31%;
        left: 43%;
    }
}

.sec6_peo_2 {
    width: 260 / $setFs + rem;
    position: absolute;
    top: 50%;
    left: 46%;
    transform: translateY(-35%);
    z-index: 1;

    @media screen and (max-width: 768px) {
        width: 25%;
        position: relative;
        top: auto;
        left: 6%;
        transform: none;
        margin-top: -100 / $setFs + rem;
    }

    @media screen and (max-width: 480px) {
        width: 30%;
        margin-top: auto;
        top: -20 / $setFs + rem;
    }
}

.sec6_peo_3 {
    width: 230 / $setFs + rem;
    position: absolute;
    bottom: 18%;
    right: 5%;
    z-index: 1;

    @media screen and (max-width: 768px) {
        width: 20%;
        bottom: 2%;
        right: 10%;
    }

    @media screen and (max-width: 480px) {
        width: 25%;
    }
    .dot{
        top: 57%;
        left: 40%;
    }
}