.sec5 {
    z-index: 2;

    @media screen and (max-width: 768px) {
        background-image: url('bg.jpg');
        background-size: cover;
        background-position: center bottom;
        padding-bottom: 120 / $setFs + rem;
    }
}

.sec5_title {
    width: 307 / $setFs + rem;
    position: absolute;
    top: -30 / $setFs + rem;
    left: 50%;
    transform: translateX(-50%);

    @media screen and (max-width: 768px) {
        width: 20%;
        top: -20 / $setFs + rem;
    }

    @media screen and (max-width: 480px) {
        width: 23%;
        top: -15 / $setFs + rem;
    }
}

.sec5_container {
    max-width: 1200 / $setFs + rem;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        max-width: 1600 / $setFs + rem;
    }

    @media screen and (max-width: 768px) {
        position: relative;
        top: auto;
        left: auto;
        transform: none;
    }

    .ind_title_en {
        letter-spacing: 1 / $setFs + rem;
    }
}

.sec5_swiper {

    @media screen and (max-width: 768px) {
        max-width: 36.25rem;
        margin: 0 auto;
        margin-top: 30 / $setFs + rem;
    }

    .swiper-slide {
        padding: 10 / $setFs + rem;
        box-sizing: border-box;
    }

    .ind_paper {
        display: flex;

        @media screen and (max-width: 768px) {
            height: auto;
            flex-direction: column;
            padding-top: 0;
        }
    }
}

.ind_paper_left {
    flex: 0 0 600 / $setFs + rem;

    @media screen and (max-width: 768px) {
        flex: 0 0 100% !important;
        order: 2;
    }
}

.ind_paper_right {
    flex: 1;
    padding-right: 48 / $setFs + rem;
    position: relative;

    @media screen and (max-width: 768px) {
        flex: 0 0 100%;
        padding: 2.5rem;
        padding-bottom: 0;
        margin-bottom: 30 / $setFs + rem;
        order: 1;
    }

    @media screen and (max-width: 480px) {
        padding: 1.5625rem;
        padding-bottom: 0;
    }
}

.sec5_pic {
    flex: 0 0 100%;
    position: relative;

    .img {
        @media screen and (max-width: 768px) {
            padding: 0 40 / $setFs + rem;
            box-sizing: border-box;
        }
    }
}

.sec5_caption {
    font-size: 15 / $setFs + rem;
    padding-left: 25 / $setFs + rem;
    padding-top: 7 / $setFs + rem;
    position: relative;

    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        font-size: (15 + 6) / $setFs + rem;
    }

    &::before {
        content: '';
        border-width: 0 5 / $setFs + rem 8 / $setFs + rem 5 / $setFs + rem;
        border-color: transparent transparent $txtColor transparent;
        border-style: solid;
        position: absolute;
        top: 15 / $setFs + rem;
        left: 3 / $setFs + rem;
    }
}

.sec5_river {
    display: none;
    position: absolute;
    mask-image: linear-gradient(230deg, black 0%, black 0%, transparent 0%);
    transform: rotate(10deg);

    @media screen and (max-width: 768px) {
        display: block;
        width: 113%;
        bottom: -10%;
        left: -15%;
    }

    @media screen and (max-width: 480px) {
        width: 120%;
    }
}

.sec6 {
    .sec5_river {
        display: block;
        width: 55%;
        position: absolute;
        top: -30%;
        left: -5%;
        mask-image: linear-gradient(230deg, black 0%, black 0%, transparent 0%);
        transform: rotate(10deg);

        @media screen and (max-width: 768px) {
            display: none;
        }
    }

}

.sec5_letters {
    width: 20%;
    position: absolute;
    bottom: 23%;
    left: 35%;
}

.sec5_peo {
    width: 13.5%;
    position: absolute;
    right: -4%;
    top: 15%;
    z-index: 2;

    @media screen and (max-width: 768px) {
        width: 25%;
        right: -12%;
        top: 30%;
    }

    @media screen and (max-width: 480px) {
        width: 19%;
        right: 5%;
        top: -25.32%;
    }
}