@import "reset";
@import "variable";
@import "base";
@import "share";
@import "components/loading";
@import "components/lightbox";
@import "components/header";
@import "components/footer";
@import "components/btn_more";

.wrapper {
    padding-top: 0;
}

.ind_content {
    max-width: 450 / $setFs + rem;
    position: relative;

    @media screen and (max-width: 768px) {
        margin: 0 auto;
    }
}

.ind_paper {
    background-color: #fff;
    position: relative;
    padding-top: 48 / $setFs + rem;
    padding-bottom: 54 / $setFs + rem;
    box-shadow: 2 / $setFs + rem 4 / $setFs + rem 10 / $setFs + rem rgba(87, 70, 56, 0.5);

    @media screen and (max-width: 768px) {
        padding-bottom: 30 / $setFs + rem;
    }

    &:before {
        content: '';
        width: 100%;
        height: 100%;
        background-image: url('bg_material.png');
        background-size: 1920 / $setFs + rem auto;
        background-position: center center;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 0;
        opacity: .7;
    }

    .container {

        @media screen and (max-width: 768px) {
            $x: 80 / $setFs + rem;
            width: calc(100% - #{$x});
        }

        @media screen and (max-width: 480px) {
            $x: 50 / $setFs + rem;
            width: calc(100% - #{$x});
        }
    }
}

.ind_title_row {
    margin-bottom: 30 / $setFs + rem;
}

.ind_title {
    font-size: (26 + 1) / $setFs + rem;
    font-family: $font2;
    letter-spacing: 4 / $setFs + rem;
    text-align: center;
    margin-bottom: 2 / $setFs + rem;

    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        font-size: (26 + 6) / $setFs + rem;
    }
}

.ind_title_en {
    font-size: 14 / $setFs + rem;
    font-family: $font2;
    font-weight: 100;
    text-transform: uppercase;
    letter-spacing: 2 / $setFs + rem;
    text-align: center;

    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        font-size: (14 + 6) / $setFs + rem;
    }
}

.ind_subtitle {
    font-size: (17 + 1) / $setFs + rem;
    font-family: $font2;
    font-weight: 100;
    text-transform: uppercase;
    letter-spacing: 4 / $setFs + rem;
    text-align: center;
    margin-bottom: 5 / $setFs + rem;

    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        font-size: (18 + 6) / $setFs + rem;
    }
}

.ind_txt {
    font-weight: bold;
    line-height: 1.8;
    letter-spacing: 2 / $setFs + rem;
    margin-bottom: 15 / $setFs + rem;


    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        font-size: ($setFs + 6) / $setFs + rem;
    }

    h4 {
        font-size: 19 / $setFs + rem;
        margin-bottom: 5 / $setFs + rem;

        @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
            font-size: (19 + 6) / $setFs + rem;
        }
    }

    p {
        margin-bottom: 20 / $setFs + rem;
    }

    a {
        display: inline;
        border: 1px solid $txtColor;
        padding: 0 5 / $setFs + rem;

        &:hover {
            border-color: transparent;
            border-bottom: 1px solid $txtColor;
        }
    }

    figcaption {
        font-size: 13 / $setFs + rem;
        padding-top: 6 / $setFs + rem;
        padding-left: 25 / $setFs + rem;
        position: relative;

        @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
            font-size: (14 + 5) / $setFs + rem;
        }

        &::before {
            content: '';
            border-width: 0 5 / $setFs + rem 8 / $setFs + rem 5 / $setFs + rem;
            border-color: transparent transparent $txtColor transparent;
            border-style: solid;
            position: absolute;
            top: 15 / $setFs + rem;
            left: 3 / $setFs + rem;
        }
    }

    table {
        border: 1px solid rgba(0, 0, 0, 0.2);
        box-sizing: border-box;

        @media screen and (max-width: 480px) {
            border: 0;
        }
    }

    td,
    th {
        border: 1px solid rgba(0, 0, 0, 0.2);
        border-spacing: 2 / $setFs + rem;
        padding: 7 / $setFs + rem 10 / $setFs + rem;

        @media screen and (max-width: 480px) {
            border: 0;
            padding: 0;
        }
    }

    th {
        @media screen and (max-width: 480px) {
            display: none;
        }
    }

    tr {
        @media screen and (max-width: 480px) {
            display: block;
            border-bottom: 1px solid rgba(0, 0, 0, 0.2);
            padding: 0.4rem 0;
        }
    }

    td {
        @media screen and (max-width: 480px) {
            display: block;
            color: #666;
        }
    }

    .th1,
    .th2 {
        width: 5rem;
        white-space: nowrap;
    }

    .th3 {
        width: 35%;
    }

    .m_title {
        display: none;

        @media screen and (max-width: 480px) {
            display: inline-block;
            font-weight: bold;
            color: $txtColor;

            &::after {
                content: ':';
            }
        }
    }
}

.ind_txt_en {
    font-size: 12 / $setFs + rem;
    line-height: 1.8;
    letter-spacing: 0;
    font-weight: bold;
    margin-bottom: 10 / $setFs + rem;

    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        font-size: (12 + 6) / $setFs + rem;
    }
}

.ind_pic {
    position: relative;
    margin-bottom: 20 / $setFs + rem;
}

.ind_paper_footer {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: -15 / $setFs + rem;

    @media screen and (max-width: 768px) {
        margin-bottom: 0;
    }

    .btn_more {
        background-image: url("more_circle_dark.png");
    }
}

.qrcode {
    width: 96 / $setFs + rem;
}
.qrcode_txt{
    font-size: 12 / $setFs + rem;
    text-align: center;

    @media screen and (min-device-width: 1200px) and (max-device-width: 2600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
        font-size: (12 + 3) / $setFs + rem;
    }
}

#kv_scene {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
}

.layer {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    pointer-events: all;

    @media screen and (max-width: 480px) {
        position: relative;
        height: auto;
    }
}

.paper_layer {
    position: relative;
    height: auto;
    pointer-events: all;
}

$dotColor: #c45a1e;

.dot {
    width: 20 / $setFs + rem;
    height: 20 / $setFs + rem;
    border-radius: 20 / $setFs + rem;
    background: $dotColor;
    position: absolute;
    z-index: 10;

    &:before,
    &:after {
        content: "";
        width: 50 / $setFs + rem;
        height: 50 / $setFs + rem;
        background: rgba($dotColor, .7);
        border-radius: 50 / $setFs + rem;
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: -1;
        transform: translateY(-50%);
    }

    &:before {
        animation: ring_1 1.8s cubic-bezier(0.25, 1, 0.5, 1) infinite;
    }

    &:after {
        animation: ring_2 1.8s cubic-bezier(0.25, 1, 0.5, 1) infinite;
    }
}

@keyframes ring_1 {
    0% {
        opacity: 1;
        transform: translate(-50%, -50%) scale(0.5);
    }

    100% {
        opacity: 0;
        transform: translate(-50%, -50%) scale(1.1);
    }
}

@keyframes ring_2 {
    0% {
        opacity: 1;
        transform: translate(-50%, -50%) scale(0.5);
    }

    100% {
        opacity: 0;
        transform: translate(-50%, -50%) scale(1.6);
    }
}


.swiper-button-prev,
.swiper-button-next {
    width: 53 / $setFs + rem;
    height: 53 / $setFs + rem;
    border-radius: 50 / $setFs + rem;
    background-color: #fff;
    background-image: url('bg_material.png');
    background-size: 1920 / $setFs + rem auto;
    background-position: center center;

    &:before {
        content: '';
        width: 100%;
        height: 100%;
        background-image: url("swiper_btn.png");
        background-size: contain;
        background-position: center center;
        position: absolute;
        top: 0;
        left: 0;
    }

    &:after {
        font-size: 0;
    }

    &:hover {
        background-image: none;
    }
}

.swiper-button-prev {
    margin-left: -100 / $setFs + rem;

    @media screen and (max-width: 480px) {
        margin-left: -20 / $setFs + rem;
    }
}

.swiper-button-next {
    transform: scaleX(-1);
    margin-right: -100 / $setFs + rem;

    @media screen and (max-width: 480px) {
        margin-right: -20 / $setFs + rem;
    }
}

.swiper-pagination {
    width: 100%;
    bottom: auto;
    top: 100%;
    margin-top: 20 / $setFs + rem;
}

.swiper-pagination-bullet {
    width: 12 / $setFs + rem;
    height: 12 / $setFs + rem;
    background-color: #fff;
    opacity: 1;
    margin: 0 10 / $setFs + rem;
    box-shadow: 1 / $setFs + rem 1 / $setFs + rem 4 / $setFs + rem rgba(87, 70, 56, 0.5);

    &:hover {
        background-image: url('sec2_river.png');
        background-size: 1920 / $setFs + rem auto;
        background-position: center center;
    }
}

.swiper-pagination-bullet-active {
    background-image: url('sec2_river.png');
    background-size: 1920 / $setFs + rem auto;
    background-position: center center;
}

@import "views/ind_kv";
@import "views/ind_sec1";
@import "views/ind_sec2";
@import "views/ind_sec3";
@import "views/ind_sec4";
@import "views/ind_sec5";
@import "views/ind_sec6";