.sec3 {
    background-image: url('bg.jpg');
    background-size: cover;
    background-position: center bottom;
    padding-top: 506 / $setFs + rem;
    padding-bottom: 220 / $setFs + rem;

    @media screen and (max-width: 768px) {
        padding-top: 50 / $setFs + rem;
        padding-bottom: 100 / $setFs + rem;
    }
}

.sec3_scene {
    width: 100%;
    position: absolute;
    top: 123 / $setFs + rem;
    right: 0;

    @media screen and (max-width: 768px) {
        position: relative;
        top: auto;
    }

    @media screen and (max-width: 480px) {
        width: 130%;
        left: -30%;
    }
}

.sec3_scene_2 {
    width: 100%;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-98%);
    z-index: 1;
}

.sec3_scene_3 {
    position: relative;
}

.sec3_letters {
    width: 523 / 3543 * 100%;
    position: absolute;
    top: 50%;
    right: 240 / 1920 * 100%;
    transform: translateY(-10%);
}

.sec3_swiper {
    position: relative;

    .swiper-slide {
        padding: 10 / $setFs + rem;
        box-sizing: border-box;

        @media screen and (max-width: 768px) {
            padding-bottom: 13 / $setFs + rem;
        }
    }
}

#sec3_scene {
    width: 580 / $setFs + rem;
    height: 720 / $setFs + rem;
    position: absolute !important;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) !important;

    @media screen and (max-width: 768px) {
        display: flex;
        flex-direction: column;
        position: relative !important;
        top: auto;
        left: auto;
        transform: none !important;
        height: auto;
        z-index: 5;
    }

    @media screen and (max-width: 480px) {
        width: 100%;
    }

    .layer {
        height: 85%;

        @media screen and (max-width: 768px) {
            position: relative;
            height: auto;
        }
    }
}

.sec3_item_1 {
    width: 300 / $setFs + rem;
    position: absolute;
    bottom: -100 / $setFs + rem;
    right: -310 / $setFs + rem;

    @media screen and (max-width: 768px) {
        width: 220 / $setFs + rem;
        right: auto;
        bottom: auto;
        left: 40%;
        top: -300 / $setFs + rem;
    }

    @media screen and (max-width: 480px) {
        width: 27%;
        left: 50%;
        top: -130 / $setFs + rem;
    }

    .dot {
        top: 36%;
        left: 20%;
    }
}

.sec3_peo {
    width: 20.5rem;
    position: absolute;
    bottom: -15 / $setFs + rem;
    left: -430 / $setFs + rem;

    @media screen and (max-width: 768px) {
        width: 260 / $setFs + rem;
        left: -60 / $setFs + rem;
        bottom: 0;
        margin-bottom: -30 / $setFs + rem;
    }

    @media screen and (max-width: 480px) {
        width: 200 / $setFs + rem;
        left: 0;
    }
}

.sec3_content {
    max-width: 580 / $setFs + rem;
    margin: 0 auto;

    .container {
        padding: 0 10 / $setFs + rem;
        box-sizing: border-box;
    }
}