.kv {
    height: 100vh;
    background-image: url('kv_bg.jpg');
    background-size: cover;
    background-position: center bottom;

    &:before {
        content: '';
        width: 100%;
        height: 100%;
        background-image: url('bg_material.png');
        background-size: cover;
        background-position: center bottom;
        position: absolute;
        top: 0;
        left: 0;
        transform: scaleY(-1);
    }

    .layer {
        @media screen and (max-width: 480px) {
            position: absolute;
            height: 100%;
        }
    }
}

.kv_lines_left {
    width: 690 / 3543 * 100%;
    position: absolute;
    top: 0;
    left: 0;
}

.kv_lines_right {
    width: 953 / 3543 * 100%;
    position: absolute;
    top: 0;
    right: 0;
}

.kv_center {
    width: 510 / $setFs + rem;
    height: 510 / $setFs + rem;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -510 / 2 / $setFs + rem;
    margin-left: -510 / 2 / $setFs + rem;

    @media screen and (max-width: 480px) {
        width: 420 / $setFs + rem;
        height: 420 / $setFs + rem;
        margin-top: -420 / 2 / $setFs + rem;
        margin-left: -420 / 2 / $setFs + rem;
    }
}

.kv_round {
    width: 510 / $setFs + rem;
    height: 510 / $setFs + rem;
    border-radius: 600 / $setFs + rem;
    background-color: #fff;
    background-image: url('bg_material.png');
    background-size: cover;
    background-position: center top;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #58391a;

    @media screen and (max-width: 480px) {
        width: 420 / $setFs + rem;
        height: 420 / $setFs + rem;
    }
}

.kv_title {
    font-size: 75 / $setFs + rem;
    font-family: $font2;
    letter-spacing: .6em;
    text-indent: .6em;
    padding: 15 / $setFs + rem 0 18 / $setFs + rem;

    @media screen and (max-width: 480px) {
        font-size: 62 / $setFs + rem;
    }
}

.kv_subtitle {
    font-size: 26 / $setFs + rem;
    font-family: $font1;
    font-weight: 500;
    text-align: center;
    letter-spacing: .5em;
    text-indent: .5em;
    margin-top: -0.8rem;

    @media screen and (max-width: 480px) {
        font-size: 23 / $setFs + rem;
        margin-top: -20 / $setFs + rem;
    }
}

.kv_subtitle_en {
    font-size: 28 / $setFs + rem;
    font-family: $font2;
    font-weight: normal;
    text-align: center;
    letter-spacing: .1em;
    text-indent: .1em;

    @media screen and (max-width: 480px) {
        font-size: 20 / $setFs + rem;
    }
}

.kv_item_1 {
    width: 310 / $setFs + rem;
    position: absolute;
    top: -163 / $setFs + rem;
    left: -231 / $setFs + rem;

    @media screen and (max-width: 480px) {
        width: 250 / $setFs + rem;
        top: -100 / $setFs + rem;
        left: -100 / $setFs + rem;
    }
}

.kv_item_2 {
    width: 240 / $setFs + rem;
    position: absolute;
    bottom: -167 / $setFs + rem;
    left: -178 / $setFs + rem;

    @media screen and (max-width: 480px) {
        width: 190 / $setFs + rem;
        bottom: -150 / $setFs + rem;
        left: -50 / $setFs + rem;
    }
}

.kv_item_3 {
    width: 286 / $setFs + rem;
    position: absolute;
    top: -143 / $setFs + rem;
    right: -200 / $setFs + rem;

    @media screen and (max-width: 480px) {
        width: 250 / $setFs + rem;
        top: -100 / $setFs + rem;
        right: -100 / $setFs + rem;
    }
}

.kv_item_4 {
    width: 386 / $setFs + rem;
    position: absolute;
    bottom: -160 / $setFs + rem;
    right: -250 / $setFs + rem;

    @media screen and (max-width: 480px) {
        width: 340 / $setFs + rem;
        bottom: -130 / $setFs + rem;
        right: -150 / $setFs + rem;
    }
}