.accesskey {
    font-size: 16 / $setFs + rem;
    position: absolute;
    right: 100%;
    top: 0;
    margin-right: 10 / $setFs + rem;
    z-index: 1;
    @include acc;

    @media screen and (max-width: 768px) {
        display: none;
    }
}

.sr-only {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1 / $setFs + rem;
    margin: -1 / $setFs + rem;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1 / $setFs + rem;
}

.pb50 {
    padding-bottom: 50 / $setFs + rem;
}

.pic {
    position: relative;
}

.img {
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
}