.sec1 {
    background-image: url('sec1_bg.jpg');
    background-size: cover;
    background-position: center center;
    padding-top: 230 / $setFs + rem;
    padding-bottom: 375 / $setFs + rem;

    @media screen and (max-width: 768px) {
        background-size: 1200 / $setFs + rem auto;
        background-position: center 115%;
        background-repeat: no-repeat;
        padding-top: 80 / $setFs + rem;
        padding-bottom: 20 / $setFs + rem;

    }

    @media screen and (max-width: 480px) {
        background-size: 1400 / $setFs + rem auto;
        background-position: 60% 150%;
    }

    &:before {
        @media screen and (max-width: 768px) {
            content: '';
            width: 100%;
            height: 100%;
            background-image: url('sec1_bg.jpg');
            background-size: 1200 / $setFs + rem auto;
            background-position: center 115%;
            background-repeat: no-repeat;
            transform: scaleY(-1);
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
        }

        @media screen and (max-width: 480px) {
            background-size: 1500 / $setFs + rem auto;
            background-position: left bottom;
        }
    }
}

.sec1_container {
    max-width: 1000 / $setFs + rem;
}

.sec1_river {
    width: 100%;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-40%);
    mask-image: linear-gradient(135deg, black 0%, black 0%, transparent 0%);

    @media screen and (max-width: 768px) {
        position: relative;
        top: auto;
        bottom: auto;
        transform: none;
        padding-top: 20 / $setFs + rem;
    }

    @media screen and (max-width: 480px) {
        width: 150%;
        left: -50%;
        padding-top: 2rem;
        margin-top: -6rem;
    }
}

.sec1_letters {
    width: 2600 / 3543 * 100%;
    position: absolute;
    top: 34%;
    right: 248 / 1903 * 100%;
}

.sec1_peo {
    width: 467 / 1903 * 100%;
    position: absolute;
    bottom: 35%;
    right: 102 / 1903 * 100%;

    @media screen and (max-width: 768px) {
        width: 520 / 1903 * 100%;
    }

    @media screen and (max-width: 480px) {
        width: 580 / 1903 * 100%;
    }
}

.sec1_item_1 {
    width: 286 / $setFs + rem;
    position: absolute;
    right: -246 / $setFs + rem;
    bottom: 130 / $setFs + rem;

    @media screen and (max-width: 768px) {
        width: 245 / $setFs + rem;
        right: -215 / $setFs + rem;
    }

    @media screen and (max-width: 480px) {
        width: 42%;
        position: relative;
        right: -200 / $setFs + rem;
        bottom: 50 / $setFs + rem;
    }

    .dot {
        top: 53%;
        left: 73%;
    }
}

.sec1_item_2 {
    width: 240 / $setFs + rem;
    position: absolute;
    left: -183 / $setFs + rem;
    bottom: -176 / $setFs + rem;

    @media screen and (max-width: 768px) {
        width: 200 / $setFs + rem;
        left: -183 / $setFs + rem;
    }

    @media screen and (max-width: 480px) {
        width: 35%;
        position: relative;
        left: auto;
        bottom: auto;
        margin-top: -140 / $setFs + rem;
    }

    .dot {
        top: 7%;
        left: 35%;
    }
}