.sec2 {
    padding-top: 255 / $setFs + rem;
    padding-bottom: 420 / $setFs + rem;

    @media screen and (max-width: 768px) {
        padding-top: 80 / $setFs + rem;
        padding-bottom: 30 / $setFs + rem;
    }

    &:before {
        content: '';
        width: 100%;
        height: 100%;
        background-image: url('bg.jpg');
        background-size: cover;
        background-position: center bottom;
        position: absolute;
        top: 0;
        left: 0;
        transform: scaleY(-1);
    }
}

.sec2_container {
    display: flex;
    justify-content: flex-end;
}

.sec2_draw {
    width: 100%;
    padding-top: 18.5%;
    position: absolute;
    top: 0;
    right: 0;

    @media screen and (max-width: 768px) {
        position: relative;
        margin-top: -100 / $setFs + rem;
        z-index: 1;
    }

    @media screen and (max-width: 480px) {
        margin-top: 0 / $setFs + rem;
    }
}

.sec2_river {
    width: 100%;
    mask-image: linear-gradient(230deg, black 0%, black 0%, transparent 0%);

    @media screen and (max-width: 480px) {
        width: 150%;
    }
}

.sec2_left {
    width: 2202 / 3543 * 100%;
    position: absolute;
    bottom: 7%;
    left: 0;

    @media screen and (max-width: 768px) {
        width: 75%;
    }

    @media screen and (max-width: 480px) {
        width: 95%;
    }

    .dot {
        top: 47%;
        left: 20%;
    }
}

.sec2_right {
    width: 491 / 3543 * 100%;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-195%);
}

.sec2_item_1 {
    width: 258 / 491 * 100%;
    position: absolute;
    top: 50%;
    right: 48%;
    transform: translateY(-75%);
}

.sec2_item_2 {
    width: 356 / 2202 * 100%;
    position: absolute;
    top: 70%;
    left: 10%;
}