.sec4 {
    padding-top: 125 / $setFs + rem;
    padding-bottom: 400 / $setFs + rem;

    @media screen and (max-width: 768px) {
        padding-top: 50 / $setFs + rem;
        padding-bottom: 15rem;
    }

    @media screen and (max-width: 480px) {
        padding-bottom: 9rem;
    }

    &:before {
        content: '';
        width: 100%;
        height: 100%;
        background-image: url('bg.jpg');
        background-size: cover;
        background-position: center bottom;
        position: absolute;
        top: 0;
        left: 0;
        transform: scaleY(-1);
    }
}

.sec4_container {
    max-width: 920 / $setFs + rem;
    display: flex;
    justify-content: flex-end;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -40%);

    @media screen and (max-width: 768px) {
        position: relative;
        top: auto;
        left: auto;
        transform: none;
        margin-top: -3%;
    }
}

.sec4_content {
    max-width: 35rem;

    @media screen and (max-width: 768px) {
        max-width: 580 / $setFs + rem;
    }
}

.sec4_scene {
    display: flex;
    justify-content: flex-end;
    position: relative;
    flex-wrap: wrap;

    @media screen and (max-width: 768px) {
        flex-wrap: wrap;
    }
}

.sec4_scene_bg {
    width: 100%;
    position: absolute;
    top: 31%;
    left: 0;
}

.sec4_house_left {
    width: 1605 / 3543 * 100%;
    margin-right: 42%;
    position: relative;

    @media screen and (max-width: 768px) {
        flex: 0 0 66%;
        margin-right: 21%;
        margin-bottom: 8rem;
    }

    @media screen and (max-width: 480px) {
        flex: 0 0 95%;
        margin-right: 5%;
        margin-bottom: 6rem;
    }

    .dot {
        top: 65%;
        left: 38%;
    }
}

.sec4_peo_left {
    width: 1566 / 1605 * 100%;
    position: absolute;
    top: 39%;
    right: 22.5%;
}

.sec4_peo_left_m_img {
    display: none !important;

    @media screen and (max-width: 768px) {
        display: block !important;
        width: 51%;
        position: absolute;
        left: 104%;
        top: 15%;
    }

    @media screen and (max-width: 480px) {
        left: 91%;
        top: 23%;
    }
}

.sec4_river {
    width: 95%;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-23%);
    mask-image: linear-gradient(230deg, black 0%, black 0%, transparent 0%);

    @media screen and (max-width: 768px) {
        width: 160%;
        top: 77%;
    }

    @media screen and (max-width: 480px) {
        width: 180%;
        top: 85%;
        right: 0;
    }
}

.sec4_letters_1 {
    width: 30%;
    position: absolute;
    top: 5%;
    right: 8%;
}

.sec4_letters_2 {
    width: 30%;
    position: absolute;
    bottom: 12%;
    left: 25%;
}

.sec4_house_right {
    width: 850 / 3543 * 100%;
    margin-top: -20%;
    position: relative;

    @media screen and (max-width: 768px) {
        flex: 0 0 40%;
        margin-top: -5%;
    }

    @media screen and (max-width: 480px) {
        flex: 0 0 50%;
        margin-top: -10%;
    }
}

.sec4_peo_right {
    width: 678 / 1002 * 100%;
    position: absolute;
    top: 50%;
    right: 11.5%;
    transform: translateY(-19%);
}

.sec4_peo_right_2 {
    width: 50%;
    position: absolute;
    top: 110%;
    right: 25%;

    @media screen and (max-width: 480px) {
        top: 95%;
        right: 15%;
    }
}

.sec4_house_front {
    width: 1188 / 1002 * 100%;
    position: absolute;
    top: 50%;
    right: 0;

    .dot {
        top: 20%;
        left: 48%;
    }
}

#sec4_scene {
    .layer {
        position: relative;
    }
}